@import "../../global.scss";

.TimelineDot {
  background-color: black;
}

.MuiTimelineSeparator-root {
  flex: inherit !important;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.MuiTypography-colorTextSecondary {
  margin-top: 2% !important;
  @include mobile{
    margin-top: 8% !important;
    font-size: 60% !important;
  }
}

.MuiTypography-h6 {
  @include mobile{
    font-size: 90% !important;
  }
}

.MuiTypography-body1 {
  @include mobile{
    font-size: 70% !important;
  }
}


.intro {  //This affects tbe intro page as well. 
  background-color: white;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imgContainer {
      width: 700px;
      height: 700px;
      background-color: rgb(255, 255, 255);
      border-radius: 50%;
      display: flex;
      
      justify-content: center;
      float: right;

      @include mobile{
        align-items: flex-start;
      }

      img {
        height: 90%;

        @include mobile{
          height: 50%;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile{
        padding-left: 0;
        align-items: center;
      }

