@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    // .imgContainer {
    //   width: 700px;
    //   height: 700px;
    //   background-color: rgb(255, 255, 255);
    //   border-radius: 50%;
    //   display: flex;
      
    //   justify-content: center;
    //   float: right;

    //   @include mobile{
